import { LazyLoadImage } from 'react-lazy-load-image-component';
import { useNavigate } from 'react-router-dom';
import { HOST_API } from '../../config';
import { fNumber } from '../../utils/formatNumber';
import { fDescriptionTypeDate, fToNow } from '../../utils/formatTime';
import GradientBorderWrapper from '../wrappers/GradientBorderWrapper';
import useAuth from '../../hooks/useAuth';

export default function EventTicketDetailCard({ ticket, type }) {
    const { user } = useAuth();
    const navigate = useNavigate();
    const status =
        ticket.status === 'inprogress' ||
        (ticket.status === 'sold' && (ticket.uploader === user?._id || ticket.buyer === user?._id));

    const onTap = () => {
        if (status) {
            navigate(`/ticket/buy/${ticket._id}`);
        }
    };

    return (
        <div
            className={`w-full h-full ${
                type === 'available' ? 'cursor-pointer opacity-90 hover:opacity-100' : 'opacity-60'
            } `}
            onClick={onTap}
        >
            <GradientBorderWrapper className="w-full h-full items-start" >
                <div className="flex gap-2 justify-between p-6 items-center">
                    <div className="flex  gap-4">
                        <div className="avatar h-[50px] w-[50px] flex-1 aspect-square">
                            <LazyLoadImage
                                src={`${HOST_API}${ticket.avatar}`}
                                alt=""
                                effect="blur"
                                className="aspect-square rounded-full"
                            />
                        </div>
                        <div className="flex flex-col">
                            <h6>{fDescriptionTypeDate(ticket.dateTime)}</h6>
                            <h5 className="text-stone-400 text-[14px] overflow-hidden text-ellipsis">
                                {ticket.count > 1 && `${ticket.count}X - `}
                                {ticket.seat?.join(' , ')}
                            </h5>
                            <h6 className='text-stone-400 text-[14px]' >{fToNow(ticket.dateTime)}</h6>
                        </div>
                    </div>
                    <div className="flex gap-2 items-center">
                        <div className="flex flex-col">
                            <h5>R${fNumber(ticket.sellPrice)}</h5>
                            <h6 className="text-stone-400 text-sm">Ingresso</h6>
                        </div>
                    </div>
                </div>
            </GradientBorderWrapper>
        </div>
    );
}