import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Icon } from '@iconify/react';

import { API_EVENT, API_TICKET } from '../../config';
import useAuth from '../../hooks/useAuth';

import axios from '../../utils/axios';
import useLocales from '../../hooks/useLocales';

import EventTicketDetailCard from '../../components/cards/EventTicketDetailCard';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';
import TicketSearchField from '../../components/form/TicketSearchField';
import TicketAlertOption from './TicketAlertOption';

export default function TicketDetailList({ tickets, event, eventType, handleBack }) {
    const navigate = useNavigate();
    const { user } = useAuth();
    const { t } = useLocales();
    const [saving, setSaving] = useState(false);
    const [notify, setNotify] = useState(false);
    const [availableTickets, setAvailableTickets] = useState([]);
    const [soldTickets, setSoldTickets] = useState([]);
    const [sort, setSort] = useState("");

    const onSell = () => {
        axios
            .post(API_TICKET.saveTicketEvent, {
                eventId: event?._id,
                typeId: eventType?.typeId
            })
            .then((res) => {
                navigate('/ticket/sell/add');
            })
            .catch((err) => { })
            .finally(() => { });
    };

    const onSort = () => {
        if (sort === "up")
            setSort("down");
        else
            setSort("up");
    }

    const onChange = (e) => {
        setSaving(true);
        axios
            .post(API_EVENT.configNotify, {
                eventId: event?._id,
                notify: e.target.checked
            })
            .then((res) => {
                if (res.status === 200) {
                    setNotify(e.target.checked);
                }
            })
            .finally(() => {
                setSaving(false);
            });
    };

    const setTickets = (_tickets) => {
        const _availableTickets = _tickets?.filter((t) => t.status === 'inprogress');
        const _soldTickets = _tickets?.filter((t) => t.status === 'sold');
        setAvailableTickets(_availableTickets);
        setSoldTickets(_soldTickets);
    }

    useEffect(() => {
        if (user) setNotify(event?.interests?.includes(user?._id) || false);
    }, [event, user]);

    useEffect(() => {
        let sortedTickets;
        if (sort === "down") {
            sortedTickets = tickets?.sort((a, b) => (b.sellPrice - a.sellPrice));
        }
        else if (sort === "up")
            sortedTickets = tickets?.sort((a, b) => (a.sellPrice - b.sellPrice));
        setTickets(sortedTickets);
    }, [sort])

    useEffect(() => {
        setTickets(tickets);
    }, [])

    return (
        <div className="flex flex-col gap-8 w-full">
            <div className="flex w-full justify-between">
                <button className="btn btn-primary btn-outline px-8 py-3.5 text-[19px] font-normal leading-6 h-auto rounded-2xl border-2 capitalize" onClick={handleBack}>
                    {t('action.all_tickets')}
                </button>
                <button className="btn btn-primary px-8 py-3.5 text-[19px] font-normal leading-6 h-auto rounded-2xl border-2 capitalize" onClick={onSell}>
                    {t('action.sell_ticket')}
                </button>
            </div>
            <div className="flex flex-col mb-8">
                <GradientBorderWrapper>
                    <div className="w-full py-[29px] px-6 flex items-center justify-between">
                        <div className="flex gap-4 items-center">
                            <div className="rounded-[12px] bg-secondary w-[48px] h-[48px] flex align-center justify-center">
                                <Icon icon="akar-icons:arrow-back" className="text-primary h-full" />
                            </div>
                            <div className="flex flex-col justify-center gap-1">
                                <h5 className="text-[28px] leading-[36px]">{t('title.menu.alert')}</h5>
                                <h6 className="text-stone-400 text-sm">{t('description.alert_desc')}</h6>
                            </div>
                        </div>
                        <div>
                            {!saving && (
                                <input
                                    onChange={onChange}
                                    type="checkbox"
                                    checked={notify}
                                    className="toggle toggle-md md:toggle-lg toggle-primary"
                                />
                            )}
                            {saving && <Icon icon="line-md:loading-twotone-loop" width={24} />}
                        </div>
                    </div>
                    <div className="divider mx-2 -my-3" />

                    <div className="flex gap-2 justify-between p-4 items-center">
                        <div className="flex gap-4">
                            <Icon icon="gg:shape-hexagon" className="text-stone-400" width={22} />
                            <p className="text-stone-400 text-sm">{t('title.all_type')}</p>
                        </div>
                        <div className="flex gap-4" >
                            <button className="btn btn-sm tooltip tooltip-bottom" onClick={onSort} data-tip={t("title.price")}>
                                <Icon icon={`bx:sort-${sort || "up"}`} className="text-primary" width={22} />
                            </button>
                            <label htmlFor="alert-option-modal-check" className="text-primary underline cursor-pointer">{t('action.options')}</label>
                        </div>
                    </div>
                </GradientBorderWrapper>
                {/* <div className="flex justify-end mt-4 items-center gap-4" >
                    <TicketSearchField />
                </div> */}
            </div>
            <div className="flex flex-col gap-4">
                <h5 className="text-[28px]">
                    {t('title.available')} - <span className="text-primary">{eventType?.name || ''}</span>
                </h5>
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-2">
                    {availableTickets?.map((t, index) => (
                        <div key={index}>
                            <EventTicketDetailCard eventType={eventType} ticket={t} type="available" />
                        </div>
                    ))}
                </div>
            </div>
            <div className="flex flex-col gap-4">
                <h5 className="text-[28px]">{t('title.sold')}</h5>
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-2">
                    {soldTickets?.map((t, index) => (
                        <div key={index}>
                            <EventTicketDetailCard eventType={eventType} ticket={t} type="sold" />
                        </div>
                    ))}
                    {soldTickets.length === 0 && <div className="w-full flex">{t('description.no_data')}</div>}
                </div>
            </div>
            <TicketAlertOption />
        </div>
    );
}
