import { Icon } from '@iconify/react';
import { Link, useNavigate } from 'react-router-dom';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { HOST_API } from '../../config';

import EventTicketCard from '../../components/cards/EventTicketCard';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function EventInformationSection({ event, onDetailAction }) {
    const navigate = useNavigate();

    const calculateTicketCount = (eventType) => {
        return event?.tickets?.filter((t) => t.eventTypeId === eventType.typeId && t?.status === 'inprogress');
    };
    const onAction = (eventTypeId) => {
        if (onDetailAction) {
            navigate(`/event/${event?._id}/basic`);
            onDetailAction(eventTypeId);
        }
    };
    return (
        <div className="w-full flex flex-col gap-4 mb-8">
            {/* location & info */}
            <div className="w-full gap-2 flex flex-col justify-between lg:flex-row mb-4">
                <div className="flex flex-col sm:flex-row gap-2 w-full">
                    <div className="flex flex-col gap-2 w-full">
                        <h5 className="text-[28px]">Local</h5>
                        <div className='flex justify-between w-full' >
                            <div className='flex gap-8'>
                                {event?.location?.image ? (
                                    <LazyLoadImage src={`${HOST_API}${event?.location?.image}`} alt="event-image" effect="blur" className='rounded-xl max-w-sm w-[384px] h-[244px]' />
                                ) : (
                                    <div className="rounded-xl max-w-sm w-[384px] h-[244px] overflow-hidden px-16 p-6 flex-col gap-2 bg-white/40 flex justify-center items-center">
                                        <Icon icon="mdi:map-marker-radius-outline" width={48} />
                                        <h6>View On Map</h6>
                                    </div>
                                )}

                                <div className="flex flex-col gap-2 flex-1">
                                    <h5 className="text-[28px]">{`${event?.location?.city.name} • ${event?.location?.city.countryCode} `}</h5>
                                    <div className="flex gap-2">
                                        <Icon className="text-primary" width={20} icon="bi:calendar2-date" />
                                        <h6 className="text-[17px] text-stone-400">26 eventos próximos</h6>
                                    </div>
                                </div>
                            </div>

                            <div className="flex w-[488px] h-[195px]">
                                <GradientBorderWrapper>
                                    <div className="p-6 flex flex-col gap-2">
                                        <h5 className="text-[28px] leading-[36px]">Você está organizando este evento?</h5>
                                        <h6 className="text-stone-400 text-[14px] leading-[18px]">
                                            Reivindique este evento para tornar a experiência dos fãs ainda melhor.
                                        </h6>
                                        <Link className="text-primary underline text-[14px]" to="/">
                                            Contate-nos
                                        </Link>
                                    </div>
                                </GradientBorderWrapper>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* tickets  */}
            <div className="flex flex-col gap-2">
                <div className="mb-4">
                    <h5 className="text-[28px]">Eventos em alta</h5>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {event?.types?.map((type, index) => (
                        <div key={index}>
                            <EventTicketCard
                                eventTypeId="event_type_id"
                                count={calculateTicketCount(type).length}
                                date={type?.dateTime}
                                onAction={() => onAction(type.typeId)}
                                type={type?.name}
                            />
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}
