import { useEffect, useState } from 'react';
import { Link, useLocation, useParams } from 'react-router-dom';

import { API_EVENT } from '../../config';
import axios from '../../utils/axios';
import { fShortenNumber } from '../../utils/formatNumber';
import useLocales from '../../hooks/useLocales';

import TicketList from '../../sections/events/TicketList';
import EventBanner from '../../sections/events/EventBanner';
import EventMoreSection from '../../sections/events/EventMore';
import TicketDetailList from '../../sections/events/TicketDetailList';
import EventInformationSection from '../../sections/events/EventInfomationSection';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';

export default function EventInformation() {
    const { t } = useLocales();
    const location = useLocation();
    const { eventId, tabId } = useParams();
    const [eventTypeId, setEventTypeId] = useState();
    const [tickets, setTickets] = useState([]);
    const [currentEvent, setCurrentEvent] = useState();

    useEffect(() => {
        const type = location.state?.type;
        if (eventId) {
            axios
                .get(`${API_EVENT.getEventDetail}/${eventId}${type !== undefined ? `?type=${type}` : ''}`)
                .then((res) => {
                    setCurrentEvent(res.data.data);
                });
        }
    }, [eventId, location]);

    const onDetailAction = (eventTypeId) => {
        setEventTypeId(eventTypeId);
        if (currentEvent && currentEvent?.tickets && eventTypeId) {
            setTickets(currentEvent.tickets.filter((t) => t.eventTypeId === eventTypeId));
        }
    };

    const onBack = () => {
        setEventTypeId(null);
        setTickets(null);
    };

    return (
        <Page title="Event Detail">
            <PageBanner>
                <div className="container p-4">
                    <div className="flex flex-col gap-4 mb-8">
                        {currentEvent && (
                            <>
                                <EventBanner event={currentEvent} />
                                <div className="flex flex-col gap-4 md:flex-row justify-between mb-8">
                                    <div className="tabs">
                                        <Link
                                            state={location.state}
                                            to={`/event/${eventId}/basic`}
                                            className={`tab text-[28px] leading-[25px] font-medium text-white ${
                                                tabId === 'basic' ? 'tab-bordered tab-active' : ''
                                            }`}
                                        >
                                            {t('title.tickets')}
                                        </Link>
                                        <Link
                                            state={location.state}
                                            to={`/event/${eventId}/infomation`}
                                            className={`tab text-[28px] leading-[25px] font-medium text-white ${
                                                tabId === 'infomation' ? 'tab-bordered tab-active' : ''
                                            }`}
                                        >
                                            {t('title.infomation')}
                                        </Link>
                                        <Link
                                            state={location.state}
                                            to={`/event/${eventId}/more`}
                                            className={`tab text-[28px] leading-[25px] font-medium text-white ${
                                                tabId === 'more' ? 'tab-bordered tab-active' : ''
                                            }`}
                                        >
                                            {t('title.more')}
                                        </Link>
                                    </div>
                                    <div className="flex gap-8">
                                        <div className="flex justify-center flex-col items-center">
                                            <h5 className="text-[28px]">
                                                {currentEvent?.tickets?.filter((t) => t.status === 'inprogress').length}
                                            </h5>
                                            <h6 className="text-stone-400 text-[13px]">{t('title.available')}</h6>
                                        </div>
                                        <div className="flex justify-center flex-col items-center">
                                            <h5 className="text-[28px]">
                                                {currentEvent?.tickets?.filter((t) => t.status === 'sold').length}
                                            </h5>
                                            <h6 className="text-stone-400 text-[13px]">{t('title.sold')}</h6>
                                        </div>
                                        <div className="flex justify-center flex-col items-center">
                                            <h5 className="text-[28px]">
                                                {fShortenNumber(currentEvent?.interests?.length)}
                                            </h5>
                                            <h6 className="text-stone-400 text-[13px]">{t('title.desired')}</h6>
                                        </div>
                                    </div>
                                </div>
                                {tabId === 'basic' && !eventTypeId && (
                                    <TicketList event={currentEvent} onDetailAction={onDetailAction} />
                                )}
                                {tabId === 'basic' && eventTypeId && tickets && (
                                    <TicketDetailList
                                        tickets={tickets}
                                        event={currentEvent}
                                        eventType={currentEvent?.types?.filter((t) => t.typeId === eventTypeId)[0]}
                                        handleBack={onBack}
                                    />
                                )}
                                {tabId === 'infomation' && (
                                    <EventInformationSection event={currentEvent} onDetailAction={onDetailAction} />
                                )}
                                {tabId === 'more' && <EventMoreSection />}
                            </>
                        )}
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
