import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Icon } from '@iconify/react';

import { API_EVENT, API_TICKET } from '../../config';
import axios from '../../utils/axios';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';

import EventTicketCard from '../../components/cards/EventTicketCard';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function TicketList({ event, onDetailAction }) {
    const { user } = useAuth();
    const { t } = useLocales();
    const [saving, setSaving] = useState(false);
    const [notify, setNotify] = useState(false);
    const navigate = useNavigate();
    const calculateTicketCount = (eventType) => {
        return event?.tickets?.filter((t) => t.eventTypeId === eventType.typeId && t?.status === 'inprogress');
    };
    const onAction = (eventTypeId) => {
        if (onDetailAction) {
            onDetailAction(eventTypeId);
        }
    };
    const onSell = () => {
        axios
            .post(API_TICKET.saveTicketEventNoType, {
                eventId: event?._id
            })
            .then(() => {
                navigate('/ticket/sell/add');
            });
    };
    const onChange = (e) => {
        setSaving(true);
        axios
            .post(API_EVENT.configNotify, {
                eventId: event?._id,
                notify: e.target.checked
            })
            .then((res) => {
                if (res.status === 200) {
                    setNotify(e.target.checked);
                }
            })
            .finally(() => {
                setSaving(false);
            });
    };
    const onSearchTicket = () => {
        navigate('/event/search');
    };
    useEffect(() => {
        if (user) setNotify(event?.interests?.includes(user?._id) || false);
    }, [event, user]);

    return (
        <div className="flex flex-col gap-8 w-full">
            <div className="flex w-full justify-end">
                {/* <button className="btn btn-primary btn-outline px-4 capitalize" onClick={onSearchTicket}>
                    {t('action.search_ticket')}
                </button> */}
                <button className="btn btn-primary px-8 py-3.5 text-[19px] font-normal leading-6 h-auto rounded-2xl capitalize border-2" onClick={onSell}>
                    {t('action.sell_ticket')}
                </button>
            </div>
            <div className="flex flex-col">
                <GradientBorderWrapper>
                    <div className="w-full py-[29px] px-6 flex items-center justify-between">
                        <div className="flex gap-4 items-center">
                            <div className="rounded-[12px] bg-secondary w-[48px] h-[48px] flex align-center justify-center">
                                <Icon icon="akar-icons:arrow-back" className="text-primary h-full" />
                            </div>
                            <div className="flex flex-col justify-center gap-1">
                                <h5 className="text-[28px] leading-[36px]">{t('title.menu.alert')}</h5>
                                <h6 className="text-stone-400 text-sm">{t('description.alert_desc')}</h6>
                            </div>
                        </div>
                        <div>
                            {!saving && (
                                <input
                                    onChange={onChange}
                                    type="checkbox"
                                    checked={notify}
                                    className="toggle toggle-md md:toggle-lg toggle-primary"
                                />
                            )}
                            {saving && <Icon icon="line-md:loading-twotone-loop" width={24} />}
                        </div>
                    </div>
                </GradientBorderWrapper>
            </div>
            <div className="flex w-full flex-col gap-4">
                <h5 className="text-[28px]">{t('title.entrance_tickets')}</h5>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
                    {event?.types?.map((type, index) => (
                        <div key={index}>
                            <EventTicketCard
                                eventTypeId="event_type_id"
                                count={calculateTicketCount(type).length}
                                date={type?.dateTime}
                                onAction={() => onAction(type.typeId)}
                                type={type?.name}
                            />
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}
