import { useNavigate } from 'react-router';
import useLocales from '../../hooks/useLocales';

export default function EventMoreSection() {
    const { t } = useLocales();
    const navigate = useNavigate();
    return (
        <div className="flex w-full justify-center flex-col gap-8 items-center p-4">
            <h4 className="text-[28px]">{t('description.want')}</h4>
            <div className="max-w-sm flex flex-col gap-4 w-full">
                <button className="btn btn-outline btn-primary capitalize py-4 leading-6 h-auto rounded-2xl w-full text-[19px]">
                    {t('action.report_postponed_event')}
                </button>
                <button className="btn btn-outline btn-primary capitalize py-4 leading-6 h-auto rounded-2xl w-full text-[19px]">
                    {t('action.report_canceled_event')}
                </button>
                <button className="btn btn-outline btn-primary capitalize py-4 leading-6 h-auto rounded-2xl w-full text-[19px]">
                    {t('action.inform_something_else')}
                </button>
                <button
                    onClick={() => navigate('/event/add')}
                    className="btn btn-outline btn-primary capitalize py-4 leading-6 h-auto rounded-2xl w-full text-[19px]"
                >
                    {t('action.claim_this_event')}
                </button>
            </div>
        </div>
    );
}
