import { useState } from 'react';
import Select from 'react-select';

import useLocales from '../../hooks/useLocales';

import { BlurModalBox } from '../../components/StyledComponents';

const options = [
    { value: 'en', label: 'English' },
    { value: 'pt', label: 'Português' }
];

const colourStyles = {
    control: (styles) => ({
        ...styles,
        backgroundColor: 'transparent',
        boxShadow: 'none !important',
        borderColor: 'hsl(0, 0%, 80%) !important'
    }),
    singleValue: (styles) => ({ ...styles, color: '#fff' }),
    menu: (styles) => ({ ...styles, backgroundColor: '#1c1c1c' }),
    option: (styles, { isFocused }) => ({
        ...styles,
        backgroundColor: isFocused ? '#fff' : '#1c1c1c',
        color: isFocused ? '#1c1c1c' : '#FFF'
    })
};

export default function TicketAlertOption() {
    const { t, currentLanguage, onChangeLang } = useLocales();
    const [state, setState] = useState({
        passport: false,
        friday: true,
        saturday: true,
    });

    const onChange = (key, value) => {
        setState({ ...state, [key]: !value });
    };

    return (
        <>
            <input type="checkbox" id="alert-option-modal-check" className="modal-toggle" />
            <div className="modal">
                <BlurModalBox className="modal-box backdrop-blur-lg">
                    <div className="mb-2">
                        <label
                            htmlFor="alert-option-modal-check"
                            className="btn btn-sm btn-circle absolute right-2 top-5 bg-transparent border-0"
                        >
                            ✕
                        </label>
                        <h3 className="font-medium text-base">{t('title.modal.ticket_alerts')}</h3>
                    </div>
                    <hr className="-mx-5 border-stone-500" />
                    <div className="flex flex-col gap-4 p-2 pt-4">
                        <p className="font-bold" >Ingressos de fãs - Com direito a entrada.</p>
                        <div className="flex flex-col gap-4">
                            <div className="flex justify-between items-center" >
                                <p>{t('title.modal.passport')}</p>
                                <input
                                    onChange={(e) => onChange('passport', state.passport)}
                                    type="checkbox"
                                    checked={state.passport}
                                    className="toggle toggle-md md:toggle-lg toggle-primary"
                                />
                            </div>
                            <div className="flex justify-between items-center" >
                                <p>{`${t('title.modal.friday')} (${t('title.modal.single')})`}</p>
                                <input
                                    onChange={(e) => onChange('friday', state.friday)}
                                    type="checkbox"
                                    checked={state.friday}
                                    className="toggle toggle-md md:toggle-lg toggle-primary"
                                />
                            </div>
                            <div className="flex justify-between items-center" >
                                <p>{`${t('title.modal.saturday')} (${t('title.modal.single')})`}</p>
                                <input
                                    onChange={(e) => onChange('saturday', state.saturday)}
                                    type="checkbox"
                                    checked={state.saturday}
                                    className="toggle toggle-md md:toggle-lg toggle-primary"
                                />
                            </div>
                        </div>
                    </div>
                    <div className="flex gap-2 mt-4 p-2">
                        <button className="btn btn-primary w-full py-4 capitalize bg-[#60242B] text-[#FD364D] text-[19px] hover:bg-[#60242B]/50 border-none h-auto rounded-2xl" >
                            {t('action.disable_all_alerts')}
                        </button>
                    </div>
                </BlurModalBox>
            </div>
        </>
    );
}
